@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semiBold.woff2") format("woff2"), url("../fonts/montserrat-semiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Montserrat", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  position: absolute;
  width: 100%;
  padding: 26px 0;
  z-index: 1;
}
@media (max-width: 1023px) {
  .header {
    padding: 12px 2px 0;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 21px 0;
  }
}
@media (max-width: 1023px) {
  .header__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__icon {
    width: 42px;
    height: 22px;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  flex-shrink: 0;
  user-select: none;
}
@media (max-width: 1023px) {
  .header__logo {
    padding-top: 6px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    padding-top: 0;
  }
}
.header__list {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  gap: 1px;
}
@media (max-width: 1023px) {
  .header__list {
    margin-right: -14px;
  }
}
.header__item {
  list-style-type: none;
}
.header__link {
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
  padding: 16px;
  user-select: none;
}
.header__link:hover {
  opacity: 0.7;
}
.header__link.focus-visible:focus {
  opacity: 0.7;
}
.header__link:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
    padding: 12px;
  }
}
.header__button {
  display: none;
}
@media (max-width: 767px) {
  .header__button {
    position: absolute;
    top: 6px;
    right: 0;
    display: block;
    width: 24px;
    height: 12px;
    background-color: transparent;
    cursor: pointer;
    border: none;
    z-index: 5;
    padding: 27px;
  }
  .header__button::before {
    content: "";
    position: absolute;
    top: 18px;
    right: 15px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
  .header__button:hover {
    opacity: 0.7;
  }
  .header__button.focus-visible:focus {
    opacity: 0.7;
  }
  .header__button:active {
    opacity: 0.5;
  }
}

@media (max-width: 767px) {
  .header--closed .header__list {
    display: none;
  }
  .header--nojs .header__button {
    display: none;
  }
  .header--nojs .header__list {
    position: absolute;
    min-height: 0;
    margin-top: 0;
    top: 6px;
    left: 50px;
  }
  .header--nojs .header__item {
    display: block;
  }
  .header--opened {
    position: fixed;
    top: 0;
    left: 36px;
    height: 100vh;
    background-color: #f9fbfd;
  }
  .header--opened::before {
    content: "";
    position: absolute;
    top: 0;
    left: -36px;
    width: 37px;
    height: 100vh;
    overflow: hidden;
    background-color: rgba(0, 43, 102, 0.5);
    pointer-events: none;
  }
  .header--opened .header__container {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto 1fr;
    row-gap: 22px;
    padding-left: 16px;
    background-color: #f9fbfd;
    animation: slide-in 0.3s;
    overflow: scroll;
    height: 100%;
  }
  .header--opened .header__logo {
    justify-self: flex-start;
    fill: #011c40;
  }
  .header--opened .header__navigation-list {
    display: flex;
    flex-direction: column;
    grid-column: 1/-1;
    overflow: scroll;
    height: 100%;
  }
  .header--opened .header__list {
    width: 100%;
    padding-right: 0;
    padding-left: 0;
  }
  .header--opened .header__item {
    margin-bottom: 25px;
    pointer-events: all;
  }
  .header--opened .header__link {
    color: #011c40;
    font-size: 18px;
    line-height: 22px;
    padding-left: 10px;
  }
  .header--opened .header__button {
    justify-self: flex-end;
    right: 35px;
  }
  .header--opened .header__button::after,
.header--opened .header__button::before {
    content: "";
    position: absolute;
    top: 25px;
    right: 12px;
    width: 18px;
    height: 2px;
    background-color: #011c40;
    box-shadow: none;
    pointer-events: all;
  }
  .header--opened .header__button::after {
    transform: rotate(45deg);
  }
  .header--opened .header__button::before {
    transform: rotate(-45deg);
  }
}
.logo {
  fill: #f9fbfd;
}
.logo:hover {
  opacity: 0.7;
}
.logo.focus-visible:focus {
  opacity: 0.7;
}
.logo:active {
  opacity: 0.5;
}

.main-picture {
