  .main-picture {
    background-image: image-set(url("../img/header-bg-tablet.webp") 1x, url("../img/header-bg-tablet.jpg") 1x, url("../img/header-bg-tablet@2x.webp") 2x, url("../img/header-bg-tablet@2x.jpg") 2x);
