  fill: #f9fbfd;
  margin-top: 5px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    align-self: flex-start;
    margin-top: 0;
  }
}
.footer__icon:hover {
  fill: #5bcfff;
}
.footer__icon.focus-visible:focus {
  fill: #5bcfff;
}
.footer__icon:active {
  fill: #5b81ff;
}
@media (max-width: 1023px) {
  .footer__icon {
    width: 38px;
    height: 20px;
  }
}

.social__list {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  list-style-type: none;
  padding: 0;
  margin: 0;
  gap: 26px;
  flex-wrap: wrap;
  padding-top: 17px;
}
@media (max-width: 1023px) {
  .social__list {
    padding-top: 12px;
    gap: 16px;
  }
}
@media (max-width: 767px) {
  .social__list {
    gap: 20px;
    padding-top: 20px;
  }
}
.social__icon {
  fill: #f9fbfd;
}
.social__icon:hover {
  fill: #5bcfff;
}
.social__icon.focus-visible:focus {
  fill: #5bcfff;
}
.social__icon:active {
  fill: #5b81ff;
}
@media (max-width: 767px) {
  .social__icon--ok {
    width: 9px;
    height: 16px;
    display: block;
  }
}

